* {
  /* background-color: rgb(247, 217, 222); */
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

:root {
  --main-title-font-size: 100px;
  --section-1-bg: #ffd0c5;
}

html {
  /* font-family: "Roboto", sans-serif; */
  font-family: "Georgia", serif;
  font-style: italic;
  text-align: center;
  /* overflow-x: hidden; */
}

.page {
  width: 100%;
  overflow-x: hidden;
}

.heading {
  position: absolute;
  top: 3%;
  z-index: 3;
  left: 50%;
  transform: translateX(-50%);
  background-color: transparent;
  text-transform: uppercase;
  color: #484848;
  letter-spacing: 5px;
  font-size: 11px;
}

.section {
  position: relative;
  top: 0%;
  left: 0%;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 225vh; */
  /* background: red; */
  /* overflow: hidden; */
}

.main-title {
  position: absolute;
  left: 50%;
  /* top: 40%;
  transform: translate(-50%, -40%); */
  top: 42%;
  transform: translate(-50%, -42%);
  font-size: var(--main-title-font-size);
  /* width: 90%; */
  width: max-content;
  max-width: 60%;
  background-color: transparent;
  /* background-color: green; */
  line-height: calc(0.95 * (var(--main-title-font-size)));
  transition: all 0.2s;
  z-index: 3;
  text-shadow: -15px 15px 10px rgba(0, 0, 0, 0.05);
}

