.scroll-down {
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -85%);
}

.scroll-down > p {
  font-style: normal;
  text-transform: capitalize;
  font-weight: bold;
  padding: 8px 15px;
  border-radius: 20px;
  color: white;
  background-color: #f55454;
}

.scroll-down-arrow {
  color: #f55454;
}

.section-1-bg {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  background-color: transparent;
}

.section-1-bg-img {
  position: absolute;
